import React from 'react';
import { Redirect, Route } from 'react-router';
import { useAuth } from '../../utils/useAuth';

function PrivateRoute({ children, ...rest }) {
  let { user } = useAuth();

  return (
    <Route
      {...rest}
      render={({ location }) => {
        return user ? (
          children
        ) : (
          <Redirect
            to={{
              pathname: '/login',
              state: { from: location },
            }}
          />
        );
      }
      }
    />
  );
}

export default PrivateRoute;
